import "./App.css";
import { BrowserRouter, NavLink, Route, Switch } from "react-router-dom";

export default function App() {
  return (
    <div className="App">
      <BrowserRouter>
        <div className="navbar">
          <span>
            <NavLink exact to="/" activeClassName="active-link">
              Home
            </NavLink>
          </span>
          <span>
            <NavLink to="/about" activeClassName="active-link">
              About
            </NavLink>
          </span>{" "}
          <span>
            <NavLink to="/details" activeClassName="active-link">
              Details
            </NavLink>
          </span>
        </div>
        <Switch>
          <Route path="/about">
            <About></About>
          </Route>
          <Route path="/details">
            <Details></Details>
          </Route>
          <Route path="/" exact>
            <Home></Home>
          </Route>
        </Switch>
      </BrowserRouter>
    </div>
  );
}

function Home() {
  return <div className="Home">Welcome to gihub hosting</div>;
}

function About() {
  return <div className="About">This is demo hosting on github</div>;
}
function Details() {
  return <div className="App">Nothing to show yet</div>;
}
